import ReactTooltip from "react-tooltip";
import "./Move.css";
const movesDescriptions = require("../move-description.json");
const MoveSet = (props) => {
  let moveSet = props.MoveSet.map((move) => {
    function getImageSource(category) {
      return (
        <img
          className={`category-img`}
          src={`${process.env.PUBLIC_URL}/${category}.png`}
          alt={`${category}`}
          width={`40px`}
        ></img>
      );
    }

    let img = getImageSource(move[3]);
    let id = `${props.Name}-${move[1]}`;
    let description = movesDescriptions[`${move[1]}`];
    let tooltip = `${description}`;
    return (
      <div key={`${move[0]}-${props.Name}-${move[1]}`} className={`move`}>
        <p
          data-for={id}
          data-tip={tooltip}
          className={`${move[2].toLowerCase()}`}
        >
          {`Lvl.${move[0]} ${move[1]} `}
          {`${move[4]} ${move[5]} `}
          {img}
        </p>
        <ReactTooltip
          id={id}
          place="bottom"
          multiline={true}
          effect="solid"
        ></ReactTooltip>
      </div>
    );
  });

  return moveSet;
};

export default MoveSet;
